import React, { Component } from 'react';
import './App.css';
import testData from './test';
import ListItem from './components/ListItem.jsx';

import io from 'socket.io-client';

class App extends Component {

  constructor(props){
    super(props);


    this.dict = {
      number: '#',
      name: 'Name',
      marketCap: 'Market Cap.',
      change: 'Change',
      price: 'Price'
    },

    this.state = {
      rawData: testData,
      filterData: testData,

      sorting: {
        number: false,
        name: false,
        marketCap: false,
        change: false,
        price: false
      }
    };
  }
  toggleSortOrder = prop => {
    const oldSorting = this.state.sorting;
    const newSorting = {};
    Object.keys(oldSorting).map(item => {
      newSorting[item] = item === prop ? !oldSorting[item] : false;
    });
    this.setState({sorting: newSorting});
  }
  sort = (prop, desc = true)  => {
    const filterData = this.state.rawData.slice();

    if(typeof filterData[0][prop] === 'number'){
      filterData.sort((a, b) => b[prop] - a[prop]);
    } else{
      filterData.sort();
    }
    this.setState({filterData: desc ? filterData : filterData.reverse()});
  }
  handleSort = prop => () => {
    const order = this.state.sorting[prop];
    this.sort(prop, order);
    this.toggleSortOrder(prop);
  }
  componentDidMount(){
    this.socket = io();
    this.socket.on('event', (msg) => {
      console.log(msg);
    });
    // this.sort('price', 1)();
  }
  render() {
    const orderArrow = prop => this.state.sorting[prop] ? <span className='arrow arrowUp'></span> :  <span className='arrow arrowDown'></span>;

    const renderHeadList = Object.keys(this.dict).map(item => {
      return <button className={item+' sorting'} onClick={this.handleSort(item)}>{this.dict[item]}{orderArrow(item)}</button>;
    });

    const renderList = this.state.filterData.map(item => {
      return <ListItem data={item} key={item.number}/>;
    });


    return (
      <div className="App">
        <header className="App-header">
          <div className='logo'>Cryproprice</div>
          <button>GitHub</button>
        </header>
        <main>
          <div className='table'>
            <div className='table-head'>
              { renderHeadList }
            </div>
            <div className='table-items'>
              { renderList }
            </div>
          </div>
        </main>
      </div>
    );
  }
}

export default App;



